import {
  useFeedbackGivenStudentList,
  useFeedbackStudentNotifcationExcelExport,
} from "../../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import Loading from "../../../../Loader/Loading";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../../UserStaffMember/PlateformUser/PlateformUser.module.css";
import { useTranslation } from "react-i18next";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import GivenStudentFeedbackListCard from "./GivenStudentFeedbackListCard";
import { pdfCdnUrl } from "../../../../Services/UrlConfig/BaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
const GivenStudentFeedbackList = ({ onClose, ifid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [disabled, setDisabled] = useState(false);

  const [feedbackStudentNotifcationExcelExport] =
    useFeedbackStudentNotifcationExcelExport();
  const { feedbackGivenStudentList, feedbackGivenStudentListLoading } =
    useFeedbackGivenStudentList({
      data: {
        ifid: ifid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !ifid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (feedbackGivenStudentList?.list?.length) {
      let obj = nextPage(feedbackGivenStudentList?.list?.length);
      setState(() => obj);
    }
  }, [feedbackGivenStudentList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onExport = () => {
    if (ifid) {
      setDisabled((pre) => !pre);
      feedbackStudentNotifcationExcelExport({
        ifid: ifid,
      })
        .then((res) => {
          if (res?.data?.excel_key) {
            setDisabled((pre) => !pre);
            setTimeout(() => {
              const newWindow = window.open(
                `${pdfCdnUrl}/${res?.data?.excel_key}`,
                "_blank",
                "noopener,noreferrer"
              );
              if (newWindow) newWindow.opener = null;
              setDisabled((pre) => !pre);
              onClose();
            }, 2000);
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };
  //   console.info("feedbackOneStaffAnalytic", feedbackOneStaffAnalytic);
  return (
    <>
      <Modal onClose={onClose}>
        <div
          className={style.modal_container}
          style={{
            maxWidth: "60rem",
          }}
        >
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("feedback_given_student_list")}</h6>
              </div>
              <div className={style.modal_container_header}>
                <CreateButton
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 1rem",
                  }}
                  label="export"
                  onAction={onExport}
                />
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingBottom: "2rem",
            }}
          >
            <div className={style.assign_search_container}>
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchEvent}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>

            {feedbackGivenStudentList?.list?.map((student, index) =>
              feedbackGivenStudentList?.list?.length === index + 1 ? (
                <div key={student?._id} ref={ref}>
                  <GivenStudentFeedbackListCard student={student} />
                </div>
              ) : (
                <GivenStudentFeedbackListCard
                  student={student}
                  key={student?._id}
                />
              )
            )}
            {feedbackGivenStudentListLoading ? (
              search ? (
                <QvipleLoader />
              ) : (
                <Loading />
              )
            ) : search ? (
              feedbackGivenStudentList?.list?.length === 0 && (
                <EmptyMessage
                  title="No student found related this search."
                  customStyleContainer={{
                    marginTop: "5rem",
                  }}
                />
              )
            ) : (
              feedbackGivenStudentList?.list?.length === 0 && (
                <EmptyMessage
                  title="No student."
                  customStyleContainer={{
                    marginTop: "5rem",
                  }}
                />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default GivenStudentFeedbackList;
